import react from "react";
import Headroom from "react-headroom";
import "./Header.scss"
import ToggleSwitch from "../ToggleSwitch/ToggleSwitch"

const Header=()=>{
    return (
        <Headroom>
            <header className="header">
                <a href="/" className="logo">
                    <span>&lt;</span>
                    <span className="logoHeading">Jaish Chimnani</span>
                    <span>/&gt;</span>
                </a>
                <ul className="navbar">
                    <li className="nav-item"><a href="/skills">Skills</a></li>
                    <li className="nav-item"><a href="/about">About Me</a></li>
                    <li className="nav-item"><a href="/experiences">Experiences</a></li>
                    <li className="nav-item"><a href="/blogs">Blogs</a></li>
                    <li className="nav-item"><a href="/projects">Projects</a></li>
                    <li className="nav-item"><a href="/contact">Contact Me</a></li>
                    
                <ToggleSwitch />
                </ul>
            </header>
        </Headroom>
    );
}

export default Header;